<template>
  <div class="main">
    <PageHeader></PageHeader>

    <div class="container">
      <div class="section2">
        <div class="top">
          <div class="title">
            <div class="left"></div>
            <div class="right" @click="setClipdata(userurl)"></div>
          </div>
          <div class="userurl">{{ userurl }}</div>
        </div>

        <div class="table">
          <div class="title">
            <div class="name"></div>
            <div class="right" @click="getMoney"></div>
          </div>
          <div class="wrap">
            <div class="li thead">
              <div class="td td1"></div>
              <div class="td"></div>
              <div class="td"></div>
              <div class="td"></div>
            </div>
            <div class="nodata">
              
            </div>
          </div>
        </div>

        <div class="bottom">
          <div class="name"></div>
          <div class="wrap">
            <div class="li">1;</div>
            <div class="li">2150%25%;</div>
            <div class="li">3</div>
          </div>
        </div>
      </div>



    </div>



  </div>
</template>

<script>
const key = "MySecretKeyCtx";
const CryptoJS = require("crypto-js");
// console.log('CryptoJS==>>', CryptoJS);
import { Toast, Dialog } from 'vant';
export default {
  name: 'user',
  data() {
    return {
      userinfo: '',
      userurl: '',
      Invitationcode: ''
    }
  },
  computed: {
    transformT() {
      return function (time) {
        let times = new Date(time)
        // return times[0];
        let Y = times.getFullYear()
        let M = times.getMonth() + 1
        let D = times.getDate()
        let h = times.getHours().toString().padStart(2, '0')
        let m = times.getMinutes().toString().padStart(2, '0')
        let s = times.getSeconds().toString().padStart(2, '0')
        return `${Y}/${M}/${D} ${h}:${m}:${s}`;
      }
    },
  },
  mounted() {
    let userinfo = localStorage.getItem('userinfo');

    if (!userinfo) {
      this.$router.push('/login');
      return;
    }
    this.userinfo = JSON.parse(userinfo)
    this.generateRandomString()
  },
  methods: {
    getMoney() {
      Toast.fail('');
    },
    generateRandomString() {
      this.Invitationcode = this.encrypt(this.userinfo.email,key)
      this.userurl = `${location.host}/login?Invitationcode=${this.Invitationcode}`

      // 
      let result = this.decrypt(this.Invitationcode, key)
      console.log('result', result);
    },
    // 
    encrypt(message, key) {
      const encryptedMessage = CryptoJS.AES.encrypt(message, key).toString();
      return encryptedMessage;
    },
    // 
    decrypt(encryptedMessage, key) {
      const decryptedBytes = CryptoJS.AES.decrypt(encryptedMessage, key);
      const decryptedMessage = decryptedBytes.toString(CryptoJS.enc.Utf8);
      return decryptedMessage;
    },
    openPayConfirm() {
      Dialog.confirm({
        title: '',
        confirmButtonText: '',
        cancelButtonText: '',
        confirmButtonColor: '#086de1',
        message: '',
      })
        .then(() => {
          // on confirm
          localStorage.clear()
          location.reload()
        })
        .catch(() => {
          // on cancel

        });
    },
    setClipdata(data) {
      try {
        navigator.clipboard.writeText(data);
        Toast.success('')
      } catch (e) {
        const textarea = document.createElement('textarea');
        textarea.setAttribute('readonly', 'readonly');
        textarea.value = data;
        document.body.appendChild(textarea);
        textarea.select();
        if (document.execCommand('copy')) {
          document.execCommand('copy');
          Toast.success('')
        }
        document.body.removeChild(textarea);
      }
    },
  }
}
</script>

